<template>
  <el-row class="fromto-time-picker-component">
    <el-row class="label-name">{{labelName}}</el-row>
    <el-date-picker
    v-model="timePickerVal"
    type="datetimerange"
    range-separator="至"
    start-placeholder="开始时间"
    end-placeholder="结束时间"
    format="yyyy-MM-dd HH:mm:ss"
    value-format="yyyyMMddHHmmss"
    class="timepicker-com"
    @change="fromToTimePickerChange(labelId)">
    </el-date-picker>
  </el-row>
</template>
<style src="./fromToTimePickerComponent.css" type="text/css"></style>
<script>
export default {
  name: "FromToTimePickerComponent",
  props: ["labelName", "compVal", "labelId"],
  data() {
    return {
      timePickerVal: this.compVal //不能在子组件内直接改变传过来的props
    };
  },
  watch: {
    compVal: function(val){
      this.timePickerVal = val;
    }
  },
  methods: {
    fromToTimePickerChange(labelId) {
      const datePickerTemp = {
        compVal: this.timePickerVal,
        labelId
      };
      this.$emit("fromToTimePickerChange", datePickerTemp); //向上级组件传递最新的值
    }
  }
};
</script>




